import React, { useState } from "react";
import { Button, Modal, Form } from "react-bootstrap";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import Swal from "sweetalert2";

import {
  payCard,
  resetCardSelected,
  saveMoney,
  withdrawMoney,
} from "../../actions/account";
import { hideModal } from "../../actions/modal";
import { validateMount } from "../../helpers/validateMount";
import { useForm } from "../../hooks/useForm";
import { OneCard } from "./OneCard";
import "./AppModal.css";

export const AppModal = () => {
  const dispatch = useDispatch();
  const { allCards, cardSelected } = useSelector((state) => state.account);
  const { isShowing, title, desc, active } = useSelector(
    (state) => state.modal
  );
  const [formValues, handleInputChanges, reset] = useForm({
    mount: "",
  });
  const [check, setCheck] = useState(false);
  const { mount } = formValues;
  const creditCard = allCards?.find((card) => card.type === "credit");

  const handleClose = () => {
    dispatch(hideModal());
    dispatch(resetCardSelected());
    reset("");
    setCheck(false);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    sendAmount(mount, active);
  };

  const handleCheckBox = (total) => {
    setCheck(!check);
    if (!check) {
      formValues.mount = total;
    } else {
      formValues.mount = "";
    }
  };

  const sendAmount = (amount, active) => {
    if (!validateMount(amount)) {
      return Swal.fire(
        "Error",
        "La cantidad debe ser un numero y no debe ser negativo",
        "error"
      );
    }

    const parseMount = parseFloat(mount);

    switch (active) {
      case "pay":
        setCheck(false);
        return dispatch(payCard(parseMount, reset));
      case "save":
        return dispatch(saveMoney(parseMount, reset));
      case "withdraw":
        if (cardSelected) {
          return dispatch(withdrawMoney(parseMount, cardSelected.type, reset));
        } else {
          return Swal.fire(
            "Selecciona una tarjeta",
            "Debes seleccionar una tarjeta para poder hacer un retiro",
            "warning"
          );
        }
      default:
        break;
    }
  };

  return (
    <>
      <Modal show={isShowing} onHide={handleClose}>
        <Modal.Header closeButton></Modal.Header>
        <Modal.Title className="mx-auto text-center">{title}</Modal.Title>

        <Form className="mt-2 px-4" onSubmit={handleSubmit}>
          <Form.Group className="mb-3" controlId="formBasicPassword">
            <Form.Label>{desc}</Form.Label>
            <Form.Control
              name="mount"
              type="number"
              placeholder="Cantidad"
              value={mount}
              onChange={handleInputChanges}
            />
            {active === "withdraw" ? (
              <div>
                <span>Seleccione la cuenta de la que desea retirar:</span>
                <div className="withdraw__cards">
                  {allCards?.map((card) => (
                    <OneCard key={card.id} {...card} />
                  ))}
                </div>
                <p className="text-muted mt-3 mb-0">
                  En cada retiro con <strong>CREDITO</strong> se cobra un 5% de
                  interes.
                </p>
              </div>
            ) : active === "pay" ? (
              <div>
                <Form.Check
                  className="mt-3"
                  type="checkbox"
                  label={`Pagar total del adeudo: $${2000 - creditCard.mount}`}
                  checked={check}
                  onChange={() => handleCheckBox(2000 - creditCard.mount)}
                />
              </div>
            ) : (
              ""
            )}
          </Form.Group>
          <Modal.Footer>
            <Button className="btn__primary-bg" type="submit">
              Confirmar
            </Button>
            <Button variant="danger" onClick={handleClose}>
              Cerrar
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    </>
  );
};
