//import { types } from "../types/types";
import Swal from "sweetalert2";
import { fetchWithToken } from "../helpers/fetch";
import { types } from "../types/types";
import { hideModal } from "./modal";

export const getCards = () => {
  return async (dispatch) => {
    const resp = await fetchWithToken("account");
    const body = await resp.json();
    if (body.ok) {
      dispatch(allCards(body.allCards));
    }
  };
};

export const payCard = (payMount, reset = null) => {
  return async (dispatch) => {
    try {
      const resp = await fetchWithToken("account/pay", { payMount }, "PUT");
      const body = await resp.json();
      if (body?.ok) {
        Swal.fire("Pago Completado", body?.msg, "success");
        dispatch(updateCardMount(body.creditBalance, "credit"));
        dispatch(updateCardMount(body.debitBalance, "debit"));
        dispatch(hideModal());
        if (reset !== null) reset("");
      } else {
        Swal.fire("Oops!", body?.msg, "error");
      }
    } catch (error) {
      console.log(error);
      Swal.fire("Oops!", "Hubo un error en el sevidor", "error");
    }
  };
};

export const saveMoney = (increaseMount, reset = null) => {
  return async (dispatch) => {
    try {
      const resp = await fetchWithToken(
        "account/save",
        { increaseMount },
        "PUT"
      );
      const body = await resp.json();
      if (body?.ok) {
        Swal.fire("Deposito Exitoso", body?.msg, "success");
        dispatch(updateCardMount(body.debitBalance, "debit"));
        dispatch(hideModal());
        if (reset !== null) reset("");
      } else {
        Swal.fire("Oops!", body?.msg, "error");
      }
    } catch (error) {
      console.log(error);
      Swal.fire("Oops!", "Hubo un error en el sevidor", "error");
    }
  };
};

export const withdrawMoney = (withDrawMount, cardType, reset) => {
  return async (dispatch) => {
    try {
      const resp = await fetchWithToken(
        "account/withdraw",
        { withDrawMount, cardType },
        "PUT"
      );
      const body = await resp.json();

      if (body?.ok) {
        Swal.fire("Retiro exitoso", body?.msg, "success");
        dispatch(resetCardSelected());
        dispatch(updateCardMount(body.cardBalance, cardType));
        dispatch(hideModal());
        if (reset !== null) reset("");
      } else {
        Swal.fire("Oops!", body?.msg, "error");
      }
    } catch (error) {
      console.log(error);
      Swal.fire("Oops!", "Hubo un error en el sevidor", "error");
    }
  };
};

export const cardSelected = (card) => ({
  type: types.accountCardSelected,
  payload: card,
});

export const resetCardSelected = () => ({
  type: types.accountResetCardSelected,
});

const updateCardMount = (cardMount, cardType) => ({
  type: types.accountUpdateCardMount,
  payload: { cardMount, cardType },
});

const allCards = (cards) => ({
  type: types.accountGetCards,
  payload: cards,
});
