import React from "react";
import { Card } from "react-bootstrap";

import "./AppCard.css";

export const AppCard = ({ title, sub1, sub2, icon, onClick }) => {
  let someClasses = { classColor: "", classHover: "card__card-hover" };
  if (!icon) {
    someClasses.classColor = "card__main-title";
    someClasses.classHover = "";
  }
  return (
    <Card className={`mb-3 card__card-container ${someClasses.classHover}`}>
      <Card.Body>
        <Card.Title className={someClasses.classColor}>
          <i className={`${icon} card__icon-color`}></i> {title}
        </Card.Title>
        <Card.Title className="card__card-title mt-2">{sub1}</Card.Title>
        <small className="text-muted">{sub2}</small>
      </Card.Body>
    </Card>
  );
};
