import { types } from "../types/types";

const initialStaste = { allCards: [] };

export const accountReducer = (state = initialStaste, action) => {
  switch (action.type) {
    case types.accountGetCards:
      return {
        ...state,
        allCards: action.payload,
      };
    case types.accountCardSelected:
      return {
        ...state,
        cardSelected: action.payload,
      };
    case types.accountResetCardSelected:
      return {
        allCards: state.allCards,
      };
    case types.accountUpdateCardMount:
      return {
        ...state,
        allCards: state.allCards?.map((card) =>
          card.type === action.payload.cardType
            ? { ...card, mount: action.payload.cardMount }
            : card
        ),
      };
    default:
      return state;
  }
};
