import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { cardSelected } from "../../actions/account";

export const OneCard = (card) => {
  const dispatch = useDispatch();
  const { cardSelected: activeCard } = useSelector((state) => state.account);
  const active = "card__active";

  const { type, mount } = card;

  const handleCard = () => {
    dispatch(cardSelected(card));
  };

  return (
    <div
      className={`withdraw__card ${activeCard?.type === type ? active : ""}`}
      onClick={handleCard}
    >
      <div className="withdraw__card-info">
        <strong>Tarjeta de {type}o</strong>
        <span className="text-start">${mount}</span>
      </div>
      <div className="withdraw__card-icon">
        <i className="far fa-credit-card"></i>
      </div>
    </div>
  );
};
