import { types } from "../types/types";

const initialStaste = { isShowing: false };

export const modalReducer = (state = initialStaste, action) => {
  switch (action.type) {
    case types.modalShow:
      return {
        ...state,
        isShowing: true,
        title: action.payload.title,
        desc: action.payload.desc,
        active: action.payload.active,
      };
    case types.modalHide:
      return {
        isShowing: false,
      };
    default:
      return state;
  }
};
