import React, { useEffect, useState } from "react";
import { Col, Container, Row } from "react-bootstrap";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { getCards } from "../../actions/account";

import { AppCard } from "../card/AppCard";
import { cardsInfo } from "../../helpers/cardsInfo";
import "./Home.css";
import { PayCredit } from "./PayCredit";
import { SaveMoney } from "./SaveMoney";
import { Withdraw } from "./Withdraw";

export const Home = () => {
  const dispatch = useDispatch();
  const { name } = useSelector((state) => state.auth);
  const { allCards } = useSelector((state) => state.account);
  const [debitCard, setDebitCard] = useState();
  const [creditCard, setCreditCard] = useState();

  useEffect(() => {
    dispatch(getCards());
  }, [dispatch]);

  useEffect(() => {
    if (allCards?.length > 0) {
      setDebitCard(allCards.find((card) => card.type === "debit"));
      setCreditCard(allCards.find((card) => card.type === "credit"));
    }
  }, [allCards]);

  const test = () => {
    console.log("test");
  };

  return (
    <div id="app__home">
      <Container>
        <div className="home__wrapper">
          <div className="home__title text-center py-4">
            <h2 className="fw-normal">¡Bienvenido a Cash Machine!</h2>
            <h2 className="text-capitalize">{name}</h2>
          </div>

          <Row className="align-items-center">
            <Col>
              <AppCard
                {...cardsInfo(
                  "Tu credito disponible es de:",
                  creditCard?.mount,
                  "de $2,000.00"
                )}
                onClick={test}
              />
              <AppCard
                {...cardsInfo(
                  "Tu saldo en tu cuenta de ahorros es de:",
                  debitCard?.mount
                )}
              />
            </Col>

            <Col>
              <PayCredit />
              <SaveMoney />
              <Withdraw />
            </Col>
          </Row>
        </div>
      </Container>
    </div>
  );
};
