import React from "react";
import { useDispatch } from "react-redux";

import { showModal } from "../../actions/modal";
import { cardIcon } from "../../helpers/cardsInfo";
import { AppCard } from "../card/AppCard";
import { AppModal } from "../modal/AppModal";

export const PayCredit = () => {
  const dispatch = useDispatch();

  const handlePayCredit = () => {
    dispatch(showModal("Pagar Credito", "Ingrese la cantidad a pagar:", "pay"));
  };

  return (
    <>
      <div onClick={handlePayCredit}>
        <AppCard {...cardIcon("Pagar credito", "fas fa-dollar-sign")} />
      </div>
      <AppModal />
    </>
  );
};
